.input-label-div {
  position: relative;
  overflow: hidden;
  margin: 10px 0;
  height: 2.5rem;
}

.input-label-div .inp-label {
  position: absolute;
  top: 0.5rem;
  font-size: 0.8rem;
  left: 10px;
  color: rgba(0, 0, 0, 0.5);
  transition: all 0.2s;
  pointer-events: none;
}

.input-label-div .inp {
  width: 100%;
  border-radius: 5px;
  height: 100%;
  bottom: 0;
  padding-top: 2%;
  padding-left: 8px;
  color: black;
  font-size: 0.8rem;
  /* transition: all linear 0.1s; */
  border: solid 1px rgba(27, 9, 9, 0.1);
}

.input-label-div .inp:disabled + .inp-label {
  outline: none;
  color: black;
  top: 0;
  font-size: 0.7rem;
}

.input-label-div .inp:not(:-ms-input-placeholder) + .inp-label {
  outline: none;
  color: black;
  top: 0;
  font-size: 0.7rem;
}

.input-label-div .inp:not(:placeholder-shown) + .inp-label {
  outline: none;
  color: black;
  top: 0;
  font-size: 0.7rem;
}

.input-label-div .inp:focus + .inp-label {
  outline: none;
  color: black;
  top: 0;
  font-size: 0.7rem;
}

